                        </div>
                        <div class="right">
                            <v-symbol name="zhongguominshengyinhang"></v-symbol>
                        </div>
                    </div>
                    <div class="bottom">
                        <div class="left">
                            <span class="title">收款账号</span>
                            <span class="info">{{remittanceInfo.recCardNo | normalizeBankCard}}</span>
                        </div>
                        <div class="right">
                            <span class="txt txt-gray">{{remittanceInfo.recBank}}</span>
                        </div>
                    </div>
                </div>
            </div>
            <div class="check-way-wrap font-num">
                <v-cell-box :hasBorder="true">
                    <span class="title" slot="left">安全验证方式</span>
                    <span class="info" slot="middle">短信验证码</span>
                    <div class="icon-wrap" slot="right">
                        <i class="icon icon-list_icon_down-"></i>
                    </div>
                </v-cell-box>
                <v-cell-box :hasBorder="true">
                    <span class="title" slot="left">手机号</span>
                    <span class="info" slot="middle">{{remittanceInfo.recPhone | formatPhone}}</span>
                </v-cell-box>
                <v-cell-box>
                    <span class="title" slot="left">短信验证码</span>
                    <input type="tel"
                           placeholder="请输入短信验证码"
                           class="input-box"
                           slot="middle"
                           maxlength="6"
                           v-model="remittanceInfo.checkNum"
                    >
                    <div class="check-num-btn" slot="right">
                        <button class="btn" :disabled="checkNumCutTime !== ''" @click="sendCheckNum">
                            <span>{{checkNumCutTime}}</span><span v-show="checkNumCutTime">s</span><span>{{checkNumTxt}}</span>
                        </button>
                    </div>
                </v-cell-box>
            </div>
            <div class="tips txt-gray">
                <p class="title">
                    温馨提示
                </p>
                <ul class="tips-info">
                    <li class="item">
                        1. 2018年5月1日起，单卡使用短信验证码日累计不超过5万元；
                    </li>
                    <li class="item">
                        2. 大于5万元的转账，请前往网点申领key令或办理转账。
                    </li>
                </ul>
            </div>
            <div class="btn-wrap">
                <v-linear-button btnTxt="确认" @click="showPwdKeyBord" :disabled="!canNext"></v-linear-button>
            </div>
        </div>
        <v-popup v-model="show">
            <div class="popup-wrap">
                <div class="title-wrap">
                    <h1 class="title">请输入您的支付密码</h1>
                    <span class="popup-cancel" @click="closePopup">取消</span>
                </div>
                <div class="kb-wrap">
                    <ul class="point-wrap">
                        <li class="point">
                            <i class="icon icon-point" :class="{ active: pwd.length > 0 }"></i>
                        </li>
                        <li class="point">
                            <i class="icon icon-point" :class="{ active: pwd.length > 1 }"></i>
                        </li>
                        <li class="point">
                            <i class="icon icon-point" :class="{ active: pwd.length > 2 }"></i>
                        </li>
                        <li class="point">
                            <i class="icon icon-point" :class="{ active: pwd.length > 3 }"></i>
                        </li>
                        <li class="point">
                            <i class="icon icon-point" :class="{ active: pwd.length > 4 }"></i>
                        </li>
                        <li class="point">
                            <i class="icon icon-point" :class="{ active: pwd.length > 5 }"></i>
                        </li>
                    </ul>
                    <div class="kb font-num" @click="inputPwd">
                        <ul class="kb-col1">
                            <li class="item">
                                <span class="num">1</span>
                                <span class="letter"></span>
                                <div class="mark" data-num="1"></div>
                            </li>
                            <li class="item">
                                <span class="num">4</span>
                                <span class="letter">GHI</span>
                                <div class="mark" data-num="4"></div>
                            </li>
                            <li class="item">
                                <span class="num">7</span>
                                <span class="letter">PQRS</span>
                                <div class="mark" data-num="7"></div>
                            </li>
                            <li class="item">
                                <div class="mark" data-num="lpp"></div>
                            </li>
                        </ul>
                        <ul class="kb-col2">
                            <li class="item">
                                <span class="num">2</span>
                                <span class="letter">ABC</span>
                                <div class="mark" data-num="2"></div>
                            </li>
                            <li class="item">
                                <span class="num">5</span>
                                <span class="letter">JKL</span>
                                <div class="mark" data-num="5"></div>
                            </li>
                            <li class="item">
                                <span class="num">8</span>
                                <span class="letter">TUV</span>
                                <div class="mark" data-num="8"></div>
                            </li>
                            <li class="item">
                                <span class="num">0</span>
                                <div class="mark" data-num="0"></div>
                            </li>
                        </ul>
                        <ul class="kb-col3">
                            <li class="item">
                                <span class="num">3</span>
                                <span class="letter">DEF</span>
                                <div class="mark" data-num="3"></div>
                            </li>
                            <li class="item">
                                <span class="num">6</span>
                                <span class="letter">MNO</span>
                                <div class="mark" data-num="6"></div>
                            </li>
                            <li class="item">
                                <span class="num">9</span>
                                <span class="letter">WXYZ</span>
                                <div class="mark" data-num="9"></div>
                            </li>
                            <li class="item del">
                                <i class="icon icon-delete"></i>
                                <div class="mark" data-num="del"></div>
                            </li>
                        </ul>
                    </div>
                </div>
