        </v-popup>
        <v-toast v-model="showToast"
                 :toastTxt="toastTxt"
        ></v-toast>
    </v-page>
</template>

<script>
    import {vCellBox} from '../../components/cell'
    import {vInputCell} from '../../components/inputCell'
    import {vLinearButton} from '../../components/linearButton'
    export default {
        name: 'transferRemittance',
        components: {
            vCellBox,
            vInputCell,
            vLinearButton
        },
        computed: {
            // 能否点击下一步
            canNext () {
                return this.remittanceInfo.checkNum !== ''
            }
        },
        data () {
            return {
                options: {
                    scroll: true
                },
                // 转账信息
                remittanceInfo: {
                    // 转账金额
                    amount: '',
                    // 转账银行卡余额
                    balance: '',
                    // 转出账号
                    tranNo: '',
                    // 转账类型
                    tranType: '',
                    // 转账类型编号
                    tranTypeNum: '',
                    // 收款人
                    recName: '',
                    // 收款人账号,
                    recCardNo: '',
                    // 收款银行
                    recBank: '',
                    // 收款网点
                    recPoint: '',
                    // 收款人手机号
                    recPhone: '',
                    // 附言
                    postScript: '',
                    // 短信验证码
                    checkNum: ''
                },
                // 发送验证码按钮文本
                checkNumTxt: '获取验证码',
                // 发送倒计时
                checkNumCutTime: '',
                // 验证码定时器对象
                checkNumTimer: null,
                show: false,
                pwd: '',
                // toast提示框信息
                toastTxt: '',
                // 是否显示toast提示框
                showToast: false
            }
        },
        filters: {
            // 格式化  @return 5,00.00
            normalizeMoney: function (value) {
                if (!value) return ''
                value = parseFloat(value)
                value = value.toFixed(2)
                let valueStr = value.toString()
                let valueArr = valueStr.split('.')
                let val0 = valueArr[0].split('').reverse()
                let val1 = valueArr[1]
                let len = val0.length
                let arr = []
                for (let i = 0; i < len; i = i + 3) {
                    arr.unshift(val0.slice(i, i + 3).reverse().join(''))
                }
                return arr.join(',') + '.' + val1
            },
            // 格式化银行卡号 ex: 6222 **** 1234
            formatBankCard (value) {
                if (!value) return ''
                value = value.toString()
                let reg = /^(\d{4})(.*)(\d{4})$/
                value = value.replace(reg, (match, p1, p2, p3) => {
                    return p1 + ' **** ' + p3
                })
                return value
            },
            // 每四位插入一个空格 ex: 6222 1234 1234 1234 12
            normalizeBankCard (value) {
                if (!value) return
                value = value.toString()
                // 下取整
                let lastIndex = parseInt(value.length / 4) * 4
                // 存储不能四等分的最后那截
                let lastStr = value.slice(lastIndex)
                let reg = /(\d{4})/g
                let arr = value.match(reg)
                return arr.join(' ') + lastStr
            },
            // 格式化手机号 ex: 135 **** 7920
            formatPhone (value) {
                if (!value) return ''
                value = value.toString()
                let reg = /^(\d{3})(.*)(\d{4})$/
                value = value.replace(reg, (match, p1, p2, p3) => {
                    return p1 + ' **** ' + p3
                })
                return value
            }
        },
        methods: {
            // 输入密码
            inputPwd (e) {
                let target = e.target
                if (target.className.toUpperCase() !== 'MARK') return
                let val = target.getAttribute('data-num')
                if (val === 'lpp') return
                if (val === 'del') {
                    this.handleDel()
                    return
                }
                if (this.pwd.toString().length > 6) return
                this.pwd = this.pwd + '' + val
                if (this.pwd.length > 5) {
                    this.closePopup()
                    this.handlePost()
                }
            },
            // 输完密码发起请求
            // 测试直接跳转页面
            handlePost () {
                let jumpUrl = '/main/remittance/remittanceResult'
                let obj = {
                    path: jumpUrl,
                    params: this.remittanceInfo
                }
                this.jumpPage(obj)
            },
            // 删除
            handleDel () {
                if (this.pwd) {
                    this.pwd = this.pwd.slice(0, this.pwd.toString().length -1)
                }
            },
            // 显示密码键盘
            showPwdKeyBord () {
                this.pwd = ''
                this.show = true
            },
            // 关闭弹出框
            closePopup () {
                this.show = false
            },
            // 跳转页面
            jumpPage (jumpUrl) {
                window.T.go(jumpUrl)
            },
            // 获取上个页面传递的参数
            getPageParams () {
                window.T.Native.getPageParams().then((ret) => {
                    this.remittanceInfo = Object.assign(this.remittanceInfo, ret.params)
                })
            },
            // 发送短信验证码
            sendCheckNum () {
                // 校验手机号
                let isPhoneNum = window.T.Validator.phone(this.remittanceInfo.recPhone, '手机号')
                if (isPhoneNum !== true) {
                    this.toastTxt = isPhoneNum
                    this.showToast = true
                    return
                }
                this._checkNumCutTime('checkNumTxt','checkNumCutTime', 'checkNumTimer')
            },
            // 短信验证码倒计时
            _checkNumCutTime (btnTxtKey, timeKey, timerKey) {
                this[timeKey] = '60'
                let oldBtnTxt = this[btnTxtKey]
                this[btnTxtKey] = '重新发送'
                this[timerKey] = setInterval(() => {
                    this[timeKey] --
                    if (this[timeKey] < 1) {
                        this[timeKey] = ''
                        this[btnTxtKey] = oldBtnTxt
                        clearInterval(timer)
                    }
                }, 1000)
            }
        },
        created () {
            this.getPageParams()
        }
    }
</script>

